body {
  font-family: 'Open Sans', sans-serif;
}
.CENTER {
  width: 1250px;
  margin: 0 auto;
}
.DEFAULT_TEXT {
  font-size: 16px;
  line-height: 28px;
}
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Open Sans Regular'), local('OpenSans-Regular'), url(https://fonts.gstatic.com/s/opensans/v17/mem8YaGs126MiZpBA-UFVZ0e.ttf) format('truetype');
}
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local('Open Sans Bold'), local('OpenSans-Bold'), url(https://fonts.gstatic.com/s/opensans/v17/mem5YaGs126MiZpBA-UN7rgOUuhs.ttf) format('truetype');
}
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 800;
  font-display: swap;
  src: local('Open Sans ExtraBold'), local('OpenSans-ExtraBold'), url(https://fonts.gstatic.com/s/opensans/v17/mem5YaGs126MiZpBA-UN8rsOUuhs.ttf) format('truetype');
}
.center {
  width: 1250px;
  margin: 0 auto;
}
header {
  background: #f6f2f2;
  height: 665px;
}
.header__wrapper {
  width: 1250px;
  margin: 0 auto;
  padding-top: 29px;
}
.nav {
  background: #313030;
  margin-bottom: 90px;
}
.nav_menu {
  padding: 0;
  width: 505px;
  height: 89px;
  margin-left: 358px;
  color: #ffffff;
  list-style: none;
  display: flex;
}
.nav_menu a {
  box-sizing: border-box;
  height: 89px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
  color: #e0e0e0;
  text-decoration: none;
  padding: 44px 26px 34px 26px;
}
.nav_menu a:hover {
  background: #ff3f40;
}
.header__about {
  height: 550px;
  display: flex;
  justify-content: space-between;
}
.header__about__text {
  width: 610px;
}
.header__about__text h1 {
  padding: 0;
  margin: 0;
  text-transform: uppercase;
  font-weight: 800;
  font-size: 60px;
  line-height: 72px;
}
.header__about__text p {
  font-size: 16px;
  line-height: 28px;
  margin-bottom: 55px;
}
.header__about__text a {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 225px;
  padding: 20px;
  height: 55px;
  border-radius: 4px;
  text-decoration: none;
  color: #ffffff;
  background: #ff3f40;
}
.header__about__video {
  width: 550px;
  height: 358px;
  background-color: #c4c0b1;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.5);
}
article {
  width: 1250px;
  margin: 0 auto;
}
.article__services {
  box-sizing: border-box;
  margin-top: 132px;
  height: 720px;
  display: flex;
  justify-content: space-between;
}
.article__services__text {
  width: 535px;
  height: 355px;
}
.article__services__text h2 {
  font-size: 42px;
  line-height: 55px;
  font-weight: 700;
  text-transform: uppercase;
}
.article__services__text p {
  font-size: 16px;
  line-height: 28px;
  margin-bottom: 49px;
}
.article__services__text a {
  font-size: 18px;
  font-weight: bold;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 225px;
  padding: 20px;
  height: 55px;
  border-radius: 4px;
  text-decoration: none;
  color: #ff3f40;
  border: solid 2px #ff3f40;
}
.services__list {
  width: 609px;
  height: 585px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.services_item {
  width: 290px;
  height: 215px;
}
.bigger-text {
  font-size: 24px;
  font-weight: 700;
}
.adad {
  width: 10px;
  height: 10px;
  background-color: #000000;
}
.article__product {
  text-align: center;
}
.article__product a {
  text-decoration: none;
  font-size: 22px;
  text-transform: uppercase;
  font-weight: bold;
  color: #000;
}
.article__product a:hover {
  color: #ff2d2d;
}
.article__product h2 {
  text-transform: uppercase;
  font-size: 42px;
  font-weight: bold;
  text-align: center;
}
.article__product p {
  font-size: 16px;
  line-height: 28px;
}
.article__product ul {
  padding: 0;
  margin: 0 auto;
  margin-top: 85px;
  margin-bottom: 68px;
  text-align: center;
  width: 900px;
  list-style: none;
  display: flex;
  justify-content: space-between;
}
.product_list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 137px;
}
.product__item {
  height: 290px;
  width: 295px;
  background: #f5f0dd;
  margin-bottom: 20px;
}
.product__item:hover {
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.5);
}
.article__team {
  width: 465px;
}
.article__team h2 {
  font-size: 42px;
  font-weight: bold;
}
.article__team p {
  font-size: 16px;
  line-height: 28px;
}
.team__people {
  display: flex;
}
.team__people h3 {
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 700;
}
.team__people ul {
  list-style: none;
}
.team__people a {
  color: #000;
  text-decoration: none;
}
.team__people__photo {
  width: 322px;
  height: 460px;
  background: #f5f0dd;
}
.team__text__wrapper {
  display: flex;
}
